* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu',
    'Cantarell',
    'Fira Sans',
    'Droid Sans',
    'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100vh;
  overflow: hidden;
  position: relative;
}

body::after {
  content: "";
  position: absolute;
  background-image: url(data:image/png;base64,);
  right: 0;
  top: 25%;
  width: 170px;
  height: 43%;
  background-repeat: no-repeat;
  background-size: 20rem;
  background-position: left;
  opacity: 0.1;
  mix-blend-mode: plus-lighter;
  pointer-events: none;
}

.App {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100vh;
  overflow: auto;
}

.container {
  padding: 15px 30px;
}

/* start style header */
header {
  position: relative;
  height: 70px;
  transition: 0.1s
}

.nav-items {
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #fff;
  padding: 5px 30px;
  height: 90px;
  background-color: #000;
  z-index: 99999;
  position: relative;
}


.holder {
  display: flex;
  align-items: center;
  gap: 10px;
  color: rgb(97 218 251);
  margin-right: 10px;
}

header .unorderdList {
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  transition: 0.5s
}

header .unorderdList a {
  margin: 0 10px;
  text-decoration: none;
  color: #fff;
  font-size: 19px;
}

/* burger menu */
.menu {
  width: 55px;
  height: 40px;
  cursor: pointer;
  transition: 0.7s;
  display: none;
}

.menu span {
  display: block;
  margin: 8px auto;
  width: 50px;
  height: 4px;
  border-radius: 10px;
  background-color: #fff;
  padding: 0 10px;
}

.menu span:nth-child(2) {
  width: 25px;
  transform: translateX(13px);
}

.menu:hover span:nth-child(2) {
  width: 50px;
  transition: all 0.5s;
  transform: translateX(0);
}

/* end header  */
/* start body */
.header {
  margin-top: 1.5rem;
  color: #fff;
  font-size: 40px;
}

.container .unorderdList {
  padding-left: 30px;
  margin: 1rem 0;
  list-style: none;
}

.container .unorderdList li {
  position: relative;
  font-size: 20px;
  padding-block: 10px;
  color: #fff;
}

.container .unorderdList li::before {
  content: "";
  position: absolute;
  left: -20px;
  top: 50%;
  transform: translateY(-35%);
  width: 10px;
  height: 10px;
  border-radius: 100%;
  background-color: rgb(97 218 251);
}

/* responsive design */
@media(max-width:768px) {
  header .unorderdList {
    position: absolute;
    left: 0;
    top: 93px;
    display: block;
    width: 100%;
    text-align: center;
    z-index: 99;
    height: 0;
    transition: 0.5s ease-in-out;
    overflow: hidden;
    background: lightskyblue;
  }

  header .unorderdList a {
    text-decoration: none;
    cursor: pointer;
    margin: 2px 0;
  }



  header .unorderdList li {
    color: #000;
    margin: 5px 0px;
    padding: 7px 0;
  }

  header .unorderdList li:last-of-type {
    border-bottom: 2px solid #fff;
  }

  .menu {
    display: block;
  }

  .unorderdList.showItems {
    height: 185.6px;
    transition: 0.5s ease-in-out !important;
    overflow: hidden;
  }

  header .switch {
    right: 100px;
  }

}

footer {
  width: 100%;
  background-color: #fff;
  padding: 25px 0;
  text-align: center
}

/* start  making the dark mode  */
/* style the switch */
.switch {
  position: absolute;
  width: 52px;
  right: 345px;
  display: flex;
  align-items: center;
}

.sun,
.moon {
  position: absolute;
  user-select: none;
  pointer-events: none;
}

.sun {
  left: 6px;
}

.moon {
  right: 8px;
}

.switch input {
  position: relative;
  width: 23px;
  height: 23px;
  border-radius: 50%;
  border: none;
  left: 0;
  transition: 0.5s ease;
  top: 50%;
  transform: translateY(-50%);
  cursor: pointer;
}

.switch input:focus {
  outline: none;
}

.switch label {
  position: absolute;
  width: 100%;
  border: none;
  background-color: #000;
  border: 2px solid #fff;
  height: 30px;
  border-radius: 30px;
  cursor: pointer;
  box-shadow: 0 0 3px #000;
}

header.darkMode nav {
  background-color: #fff;
}

header.darkMode nav a {
  color: #000;
}

header.darkMode .menu span {
  background-color: #000;
}

.body {
  flex: 1 0 auto;
  background-color: #222;
  color: #000;
}

.body.light {
  background: #eee;
  color: #000;
}

.body.light h1,
.body.light li {
  color: #000;
}

/* end dark mode  */